.container {
  width: 100vw;
  height: 100vh;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("../../../assets/welcome.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.container::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  bottom: 0px;
  left: 0px;
  background-color: rgba(255, 255, 255, 0.97);
}
.inner {
  position: relative;
}
.hero_text {
  max-width: 1024px;
  padding: 2rem;
}
.hero_text h1 {
  font-size: 5rem;
  width: 100%;
  color: var(--s-dark);
  text-align: center;
  font-weight: 900;
  text-shadow: 2px 7px 5px rgba(0, 0, 0, 0.3),
    0px -4px 10px rgba(255, 255, 255, 0.3);
}

@media screen and (max-width: 600px) {
  .hero_text h1 {
    font-size: 4rem;
  }
}

@media screen and (max-width: 500px) {
  .hero_text h1 {
    font-size: 2.5rem;
  }
}
