.verify_email {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0rem 2rem;
}
.verify_email button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: none;
  width: 300px;
  height: 60px;
  color: white;
  font-weight: 700;
  font-size: 1.5rem;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
  letter-spacing: 2px;
  transition: all 0.2s linear;
  background-color: var(--s-dark);
}
.verify_email button:hover {
  text-shadow: none;
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.3);
  border-radius: 50px;
}

@media screen and (max-width: 600px) {
  .verify_email button {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .verify_email button {
    width: 230px;
    height: 45px;
    font-size: 1.2rem;
  }
}
